.TodoInsert {
  display: flex;
  background-color: #495057;

  input {
    // 기본 스타일 초기화
    background: none;
    outline: none;
    border: none;
    padding: 0.5rem;
    font-size: 1.125rem;
    line-height: 1.5;
    color: #fff;

    &::placeholder {
      color: #dee2e6;
    }
    
    // 버튼을 제외한 영역을 모두 차지하기
    flex: 1;
  }

  button {
    // 기본 스타일 초기화
    outline: none;
    border: none;
    background: #868e96;
    color: #fff;
    padding: 0 1rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: 0.1s background ease-in;

    &:hover {
      background: #adb5bd;
    }
  }
}